:root {
  --font-size: 14px;
  --primary-c: #007cba;
  --box-shadow: 0 2px 2px #00000038;
  --items: 1;
  --gap: 12px;
}

*, :before, :after {
  box-sizing: border-box;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

ol {
  padding-left: 0;
  list-style-position: inside;
}

