  background-color: #a1b5bf;
}

.section {
  padding: 32px 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.btn, .btn-up {
  font-size: inherit;
  color: #fff;
  font-size: inherit;
  color: #fff;
  background-color: #000;
}

.btn-up {
  width: 48px;
