  }
}

.modal.container {
  position: relative;
}

.modal__close-btn {
  width: 32px;
  height: 32px;
  background-color: #0000;
  justify-content: center;
  top: 4px;
  right: 16px;
}

@media (min-width: 768px) and (max-width: 800px) {
  .modal__close-btn {
    right: 32px;
